/******  ******/
.movie-modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}
.movie-modal {
  margin: 0 auto;
  width: 850px;
  /* background-color: var(--white-opacity-5); */
  border-radius: 16px;
  padding-bottom: 32px;
  overflow: auto;
  overflow-y: auto;
  scrollbar-width: none;
  max-height: 800px;
  -ms-overflow-style: none;
  position: relative;
  z-index: 10000;
}
.movie-modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.movie-modal::-webkit-scrollbar {
  width: 0;
  height: 0;
  display: none;
}
.movie-modal__close-btn {
  border: none;
  position: absolute;
  outline: none;
  color: var(--white);
  background: none;
  z-index: 1000;
  top: 16px;
  right: 16px;
  font-size: 18px;
}

.movie-modal__content {
  position: relative;
  width: 100%;
  z-index: 10;
}
.movie-modal_overlay_backdrop {
  width: 100%;
  height: 100vh;
  z-index: 1;
  position: absolute;
  top: 0px;
  opacity: 0.12;
  filter: blur(300px);
}
.movie-modal__poster-vignette {
  position: absolute;
  bottom: 0; /*      */
  left: 0;
  width: 100%; /*    */
  height: 100%; /*    */

  background-size: cover;
  background-position: center;
  opacity: 0.8;
  z-index: 2;
}

.movie-modal__poster {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
}
.movie-modal__header {
  position: relative;
  height: 380px;
  /* margin-bottom: 32px; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* border-bottom: 1px solid var(--white-gray); */
}
.movie-modal__header-details {
  position: relative;
  z-index: 20;
  margin-left: 32px;
  margin-right: 32px;
  margin-bottom: 70px;
  display: flex;
  flex-direction: column;
}
.movie-modal__poster-container {
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.movie-modal__title {
  color: var(--white);
  font-size: 32px;
  font-weight: 800;
  margin-bottom: 4px;
}
.movie-modal__info {
  display: flex;
  gap: 8px;
  color: var(--white);
  font-size: 14px;
  font-weight: 400;
}
.movie-modal__origin {
  border-right: 0.1px solid var(--white);
  padding-right: 8px;
  max-width: 50%;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.movie-modal__rating {
  display: flex;
  align-items: center;
  border-right: 0.2px solid var(--white);
  padding-right: 8px;
  gap: 4px;
}

.movie-modal_controls-play-btn {
  display: flex;
  gap: 8px;
  align-items: center;
}

