.movie-modal__play-button {
  background-color: var(--white);
  width: 133px;
  height: 40px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  color: var(--dark);
  cursor: pointer;
}
.movie-modal__cast-button {
  cursor: pointer;
  /* background-color: var(--white-opacity-10); */
  border-radius: 8px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.movie-modal__play-button:hover {
  background-color: var(--red);
  transition: 0.1s ease-in;
}

.movie-modal__details {
  padding-left: 32px;
  padding-right: 32px;
  padding-top: 24px;
  padding-bottom: 24px;
  border-radius: 0%;
  position: relative;
  z-index: 10000;
  background-color: var(--white-opacity-10);
  /* backdrop-filter: blur(1px); */
  width: 100%;
  top: -40px;
  border-radius: 0px 0px 24px 24px;
  background-color: var(--dark);
}
.movie-modal__details-container {
  flex-wrap: wrap;
}
.movie-modal__section-title {
  font-size: 16px;
  font-weight: 700;
  color: var(--white);
}
.movie-modal__description-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.movie-modal__translators {
  margin-top: 32px;
  display: flex;
  gap: 16px;
  flex-direction: column;
}
.movie-modal__translators-container {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}

.movie-modal__trailer {
  width: 100%;
  min-width: 300px;
  height: 195px;
  background-color: var(--gray);
  border-radius: 16px;
}

.movie-modal__details-top {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.movie-modal__release-date {
  color: var(--white-gray);
  font-weight: 500;
  font-style: 14px;
}

.movie-modal__age {
  color: var(--white-gray);
  border: 1.7px solid var(--white-gray);
  padding: 4px 8px 4px 8px;
  max-width: max-content;
  border-radius: 8px;
}

.movie-modal__description-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;

  margin-top: 16px;
  justify-content: space-between;
}

.movie-modal__description {
  max-width: 364px;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  color: var(--white);
  font-weight: 400;
  font-size: 12px;
}

.movie-modal_infotable {
  max-width: 364px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.movie-modal__infotable-lable {
  color: var(--white-gray);
  font-size: 12px;
  font-weight: 500;
}

.movie-modal__infotable-item {
  display: flex;
  gap: 4px;
  width: 100%;
  flex-wrap: wrap;
}

.movie-modal__infotable-value {
  color: var(--white);
  font-size: 12px;
  font-weight: 500;
}
.movie-modal__episodes {
  margin-top: 32px;
}
.movie-modal__episodes-container {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.movie-modal__season-selector-container {
  align-items: center;
  display: flex;
  width: 100%;
  justify-content: space-between;
}
/*   */
.movie-modal__season-selector {
  position: relative;
  display: inline-block;
}

/*    (,  ) */
.season-selector__current {
  background-color: var(--white-opacity-5);
  color: var(--white);
  font-size: 16px;
  font-weight: 500;
  border-radius: 8px;
  padding: 8px 12px;
  cursor: pointer;
  user-select: none; /*       */
  min-width: 120px; /*    ""    */
  display: flex;
  gap: 8px;
  align-items: center;
}

/*   */
.season-selector__list {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: var(--dark);
  border-radius: 8px;
  margin-top: 4px;
  z-index: 999; /*  ,     */
  width: 250px;
  display: flex;
  flex-direction: column;
  max-height: 300px; /*   5 ,   */
  overflow-y: auto;
}

/*   */
.season-selector__item {
  padding: 16px 16px;
  cursor: pointer;
  user-select: none;
  display: flex;
  gap: 8px;
  font-size: 18px;
  align-items: center;
}

/* Hover */
.season-selector__item:hover {
  background-color: var(--white-opacity-10);
}

.season-selector__item-count {
  color: var(--white);
  font-size: 14px;
  font-weight: 400;
}

.season-selector__item-title {
  color: var(--white);
  font-weight: 500;
  font-size: 18px;
}

/*    */
.season-selector__list::-webkit-scrollbar {
  width: 4px; /*   */
  background-color: transparent;
}

/*  (  ) */
.season-selector__list::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

/*   () */
.season-selector__list::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 4px;
}

/*      */
.season-selector__list::-webkit-scrollbar-thumb:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
.movie-modal__playback-controls {
  margin-top: 16px;
}

.movie-modal__continue-volume-btn {
  transition: 0.2s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.movie-modal__controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
}

.movie-modal__youtube-player-container {
  /* position: relative; */
  width: 100%;
  /* padding-top: 220 0.25%; */
  pointer-events: none;
  overflow: hidden;
}
.movie-modal__youtube-player {
  position: absolute;
  left: 0;
  top: 0;
  max-width: 100%;
  height: 100%;
  transform: scale(2);
  overflow: hidden;
}
.movie-modal__cast-button.disabled {
  opacity: 0;
  pointer-events: none;
  cursor: not-allowed;
}
