* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #1b1b1b;
  font-family: "SF Pro Display", sans-serif;
}
.container {
  max-width: 1600px;
  margin: 0 auto;

  width: 100%;
}

:root {
  --sub-gray: #1d1d1d;
  --dark: #2d2d2d;
  --white: #eeeeee;
  --green: #00c717;
  --red: #ff223f;
  --gray: #1a1a1a;
  --red-gradient-linear: linear-gradient(45deg, #ff0f2f, #ff5b20);
  --vignette: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.1) 0%,
    rgba(0, 0, 0, 0.9) 100%
  );
  --white-opacity-5: rgba(238, 238, 238, 5%);
  --white-opacity-10: rgba(238, 238, 238, 10%);
  --white-opacity-20: rgba(238, 238, 238, 20%);
  --white-opacity-30: rgba(238, 238, 238, 30%);
  --white-opacity-40: rgba(238, 238, 238, 40%);
  --white-opacity-50: rgba(238, 238, 238, 50%);
  --white-opacity-60: rgba(238, 238, 238, 60%);
  --white-opacity-70: rgba(238, 238, 238, 70%);
  --white-opacity-80: rgba(238, 238, 238, 80%);
  --white-opacity-90: rgba(238, 238, 238, 90%);
  --white-gray: rgba(238, 238, 238, 0.7);
  --dark-opacity-5: rgba(30, 30, 30, 5%);
  --dark-opacity-10: rgba(30, 30, 30, 10%);
  --dark-opacity-20: rgba(30, 30, 30, 20%);
  --dark-opacity-30: rgba(30, 30, 30, 30%);
  --dark-opacity-40: rgba(30, 30, 30, 40%);
  --dark-opacity-50: rgba(30, 30, 30, 50%);
  --dark-opacity-60: rgba(30, 30, 30, 60%);
  --dark-opacity-70: rgba(30, 30, 30, 70%);
  --dark-opacity-80: rgba(30, 30, 30, 80%);
  --dark-opacity-90: rgba(30, 30, 30, 90%);
  --gray-2: #262626;
  --blue: #0a84ff;
}

.background-glow-center-green {
  width: 200px;
  height: 200px;
  background-color: var(--blue);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /*     */
  z-index: -2;
  opacity: 0.2;
}
.background-glow-center-red {
  width: 200px;
  height: 200px;
  background-color: var(--red);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /*     */
  z-index: -2;
  opacity: 0.2;
}
.background-blur-100 {
  width: 100%;
  height: 100%;
  z-index: -1;
  position: fixed;
  backdrop-filter: blur(100px);
  background-color: rgba(0, 0, 0, 0.1);
  top: 0;
  left: 0;
}
.body-backdrop {
  position: absolute; /*  fixed       */
  top: 0;
  left: 0;
  width: 99vw;
  height: 600px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  z-index: -1;
  transition: 0.4s ease-in-out;
  filter: blur(200px); /*     */
  opacity: 0.5; /*   */
}

.scroll-info {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  padding: 1rem;
}
@media (max-width: 1650px) {
  .container {
    padding-left: 16px;
    padding-right: 16px;
  }
  .explorer-library-grid {
    grid-template-columns: 1fr 1fr 1fr 1fr; /*     */
  }
}
@media (max-width: 1650px) {
  .header-menu {
    align-items: center;
    justify-content: center;
    margin-top: 24px;
  }
}
@media (max-width: 1200px) {
  .explorer-library-grid {
    grid-template-columns: 1fr 1fr 1fr; /*     */
  }
  .collection-container {
    grid-template-columns: 1fr 1fr 1fr;
  }
  /* .video-card-container {
    max-height: 500px;
  } */
  .playback-controls-container {
    width: 800px;
  }

  /* .video-card-preview-wrapper {
    height: 160px;
  } */
}
.row-header-title {
  font-size: 24px;
  color: var(--white);
  font-weight: 600;
  cursor: pointer;
}
@media (max-width: 1024px) {
  .continue-overlay {
    -webkit-mask-image: linear-gradient(to right, black 20%, transparent 100%);
    mask-image: linear-gradient(to right, black 20%, transparent 100%);
  }
  .continue_description {
    max-width: 400px;
  }
}

@media (max-width: 900px) {
  /* .video-card-container {
    max-height: 450px;
  } */
  .playback-controls-container {
    width: 700px;
  }
  .continue-title {
    font-size: 32px;
  }
}

@media (max-width: 860px) {
  .header-menu {
    justify-content: space-between;
  }
  .hdrezka-header-input-container {
    width: 100%;
  }
}
@media (max-width: 850px) {
  .movie-modal {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    border-radius: 0px;
  }

  /* .video-card-preview-wrapper {
    height: 130px;
  } */
}

@media (max-width: 800px) {
  .player-container {
    max-width: 1185px;
    max-height: 600px;
    width: 100%;
    aspect-ratio: 9 / 16;
  }
  .playback-controls-container {
    width: 600px;
  }
  .explorer-library-grid {
    grid-template-columns: 1fr 1fr;
  }
  .collection-container {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 768px) {
  /* .video-card-container {
    max-height: 400px;
  } */

  .continue-title {
    font-size: 28px;
  }
}

@media (max-width: 600px) {
  .container {
    padding-left: 16px;
    padding-right: 16px;
  }
  .movie-modal__header-details {
    margin-left: 16px;
    margin-right: 16px;
  }
  .movie-modal__details {
    padding-left: 16px;
    padding-right: 16px;
  }
  .explorer-library-grid {
    grid-template-columns: 1fr 1fr; /*     */
  }

  .playback-controls-container {
    width: 500px;
  }
  .continue-title {
    font-size: 28px;
  }
}
@media (max-width: 680px) {
  header {
    display: block;
  }

  .body-backdrop {
    max-height: 800px;
    filter: blur(80px);
  }
  .continue-overlay {
    -webkit-mask-image: linear-gradient(to top, black 20%, transparent 100%);
    mask-image: linear-gradient(to top, black 20%, transparent 100%);
    background: linear-gradient(
      to top,
      rgba(0, 0, 0, 0.415) 100%,
      transparent 100%
    );
  }
  .continue-info {
    flex-direction: column-reverse;
    margin-top: 4px;
    margin-bottom: 16px;
  }

  .continue-content-bottom {
    justify-content: center;
    text-align: center;
    align-items: center;
    gap: 0px;
    margin-bottom: 40px;
  }
  .continue__play-button {
    width: 95%;
  }
  .continue-content {
    justify-content: space-between;
  }

  .continue-sub_title {
    flex-wrap: wrap;
    gap: 16px;
    justify-content: center;
  }
  .continue-sub_title span {
    background: none;
    border: none;
    backdrop-filter: none;
    padding: 0;
    margin: 0;
  }
  .continue-sub_title-release_date {
    display: none;
  }
  .continue__play-button {
    margin: 0;
  }
  .continue_description {
    display: none;
  }

  .continue-origin_name {
    display: none;
  }
  .continue-sub_title {
    font-size: 14px;
  }
  .movie-modal__details {
    border-radius: 16px;
  }
  .continue-volume-btn {
    top: 16px;
    right: 16px;
    bottom: auto;
  }
  .continue-container.unmuted {
    height: 480px;
  }
  .continue-container.unmuted .youtube-player {
    transform: scale(2.5);
  }
}
@media (max-width: 550px) {
  .explorer-library-grid {
    grid-template-columns: 1fr 1fr; /*     */
  }

  .movie-modal__description {
    -webkit-line-clamp: 8;
  }
  .playback-controls-container {
    width: 400px;
  }
  .continue-title {
    font-size: 24px;
    text-align: center;
  }

  .continue-overlay {
    -webkit-mask-image: linear-gradient(to top, black 10%, transparent 100%);
    mask-image: linear-gradient(to top, black 10%, transparent 100%);
  }
}

@media (max-width: 580px) {
  .youtube-player {
    transform: scale(2.8);
  }
}
@media (max-width: 500px) {
  .container {
    padding-left: 16px;
    padding-right: 16px;
  }
  .movie-modal__header-details {
    margin-left: 16px;
  }
  .movie-modal__details {
    padding-left: 16px;
    padding-right: 16px;
  }
  .movie-modal__title {
    font-size: 24px;
  }
  .device-selector-container {
    display: none;
  }
  .playback-controls-container {
    width: 350px;
  }
  .continue-content-bottom {
    margin-bottom: 32px;
  }

  .video-card-container-explorer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    align-items: center;
  }

  .explorer-library-grid {
    grid-template-columns: 1fr;
    gap: 24px 0px;
  }
  .video-card-meta {
    /* border-bottom: 1px solid rgba(255, 255, 255, 0.1); */
    height: 100%;
    justify-content: flex-end;
    margin: 0;
    width: 100%;
    /* border-bottom: 1px solid rgba(255, 255, 255, 0.1); */
  }
  .video-card-title {
    width: 100%;
    -webkit-line-clamp: 2;
  }
  .video-card-video-duration {
    white-space: wrap;
    -webkit-line-clamp: 2;
  }
  .video-card-preview-image {
  }
}

@media (max-width: 530px) {
  .explorer_watch-history .swiper-slide {
    width: 180px !important; /*   JS,   */
    /*   min-width / max-width   */
  }
  .movie_card_swiper .swiper-slide {
    width: 180px !important; /*   JS,   */
    /*   min-width / max-width   */
  }
  .video-card-preview-wrapper {
    width: 180px;
  }
  .video-card-preview-wrapper-explorer {
    width: 100%;
  }
  .user-login__avatar-container {
    width: 100px;
    height: 100px;
  }
  .login-create-user {
    width: 100px;
    height: 100px;
  }
  .header-user__dropdown.under-avatar {
    padding: 32px;
    position: fixed !important;
    left: 0;
    right: 0;
    bottom: 0;
    top: auto;
    margin: 0;
    min-width: unset;
    width: 100vw;
    max-width: 100vw;
    border-radius: 32px 32px 0 0 !important;
    z-index: 99999999;

    animation: bottom-sheet-appear 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    overflow-y: auto;
  }
  .home-page-content {
    gap: 16px;
  }

  @keyframes bottom-sheet-appear {
    from {
      transform: translateY(100%);
    }
    to {
      transform: translateY(0);
    }
  }
}
@media (max-width: 450px) {
  .continue-content-bottom {
    justify-content: center;
    text-align: center;
    align-items: center;

    margin-bottom: 40px;
  }
  .continue-content {
    justify-content: space-between;
  }

  .continue-title {
  }
  .continue-container {
    height: 600px;
  }
  .player-container {
    height: 440px;
  }
  @media (max-width: 530px) {
    /* .video-card-preview-wrapper {
      height: 100px;
    } */
  }
  .collection-container {
    grid-template-columns: 1fr 1fr;
    gap: 16px;
  }
}
.video-js {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.spinner-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  min-height: 100%;
}

.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
  border: 4px solid var(--white-opacity-10);
  border-top: 4px solid var(--blue);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

